.mess{
    display: flex;
    align-items: flex-start;
    margin-top: 41px;
}

.mess__left{
    flex: 0 0 338px;
    margin-right: 20px;
    max-width: 338px;
    overflow: hidden;
}

.mess__item{
    background-color: #fff;
    border-radius: 20px;
    padding: 6px;
    cursor: pointer;
    margin-bottom: 16px;
}
.mess__item__wrap{
    background: linear-gradient(180deg, #41c575 0%, #2fa996 100%);
    padding: 9px 10px;
    transition: 0.3s all;
    border-radius: 20px;
    position: relative;
    
}
.mess__item__wrap__row{
    display: flex; 
    position: relative;
    align-items: center;
    z-index: 2;
}
.mess__item__wrap:after{
    content: "";
    left:-1px;
    right: -1px;
    top: -1px;
    bottom: -1px;
    background-color: #fff;
    position: absolute;
    z-index: 1;
    border-radius: 0px;
    opacity: 1;
    
}
.mess__item__img{
    width: 58px;
    height: 58px;
    min-width: 58px;
    border-radius: 100%;
    background-size: cover;
    margin-right: 15px;
}

.mess__item:hover, .mess__item.active{
    .mess__item__wrap:after{
        left:3px;
        right: 3px;
        top: 3px;
        bottom: 3px;
        border-radius: 18px;
        box-shadow: inset 5px 5px 5px 1px rgba($color: #000000, $alpha: 0.1);
    }
    
}

.mess__item__text{
    flex: 1;
}
.mess__item__arr{
    width: 38px;
    height: 38px;
    min-width: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    margin-right: 12px;
    background-color: #f3f5f7;
    color: #a7b0b8;
    font-weight: bold;
    font-size: 32px;
    line-height: 1;
    padding-bottom: 8px;
    opacity: 0;
}
.mess__item.active{
    .mess__item__arr{
        opacity: 1;
    }
}
.mess__item__name{
    font-weight: 800;
}
.mess__item__status{
    color: #a7abaf;
}

.mess__right{
    flex: 1;
    background-color: #eaeaea;
    padding: 32px;
    border-radius: 20px;
}
.mess__list__item{
    background-color: #fff;
    padding: 16px 28px;
    border-radius: 20px;
    margin-bottom: 15px;
}
.mess__list__item__top{
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    .name{
        color: #3ec07a;
        font-weight: bold;
        font-size: 14px;
    }
    .data{
        color: #c4c5c8;
        font-size: 12px;
    }
}
.mess__list__item__text{
    font-size: 14px;
    line-height: 1.2;
    font-weight: 500;
}

.mess__right textarea{
    width: 100%;
    padding: 20px 25px;
    background-color: #e2e4e6;
    border: 0;
    border-radius: 20px;
    min-height: 175px;
    margin-bottom: 22px;
    resize: vertical
}

.mess__right textarea::placeholder{
    color: #bbc1c9;
}

.mess__right__bot{
    display: flex;
    justify-content: flex-end;
    .btn{
        padding: 13px 20px;
        border-radius: 20px;
    }
}
.mess__wrap {
    .swiper-wrapper{
        flex-direction: column;
    }

    .swiper-button{
        display: none;
    }
}
.dark-theme{
    .mess__item{
        background-color: #363335;
    }
    .mess__item__wrap:after{
        background-color: #363335;
    }
    .mess__item__arr{
        background-color: #403b3e;
        color: #565054;
    }

    .mess__right{
        background-color: #242123;
    }
    .mess__list__item{
        background-color: #363335;
    }
    .mess__right textarea{
        background-color: #211e20;
        color: #fff;
    }
    .mess__right textarea::placeholder{
        color: #373235;
    }
}

@media(max-width: 991px){
    .mess{
        flex-direction: column;
    }
    .mess__left{
        flex: auto;
        width: calc(100% + 36px + 36px);
        margin-left: -36px;
        margin-right: -36px;
        padding-right: 36px;
        padding-left: 36px;
        position: relative;
        max-width: initial;
        overflow: hidden;
        padding-bottom: 20px;
        padding-top: 20px;

    }
    .mess__wrap {
        .swiper-wrapper{
            flex-direction: row;
        }
        .swiper-button{
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            width: 36px;
            height: 100%;
            top: 0;
            transform: translate(0);
            margin: 0;
            background-color: #f0eeef;
            font-size: 50px;
            padding-bottom: 2%;
            color: #a7b0b8;
        }
        .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled{
            opacity: 1;
            color: transparent
        }
        .swiper-button-next{
            right: 0;
        }
        .swiper-button-prev{
            left: 0;
        }
        .swiper-button:after{
            display: none;
        }
    }
    .mess__item{
        margin-bottom: 0;
    }
    .dark-theme{
        .mess__wrap {
            .swiper-button{
                background-color: #292628;
                color: #fff;
            }
        }
    }
}

@media(max-width: 767px){
    .mess__left{
        width: calc(100% + 40px);
        margin-left: -20px;
        margin-right: -20px;
        padding-right: 43px;
        padding-left: 43px;
    }
    .mess__wrap {
        .swiper-button{
            width: 43px;
        }
    }
}

@media(max-width: 575px){
    .mess__item__img{
        height: 40px;
        width: 40px;
        min-width: 40px;
    }
    .mess__item__name{
        font-size: 12px;
    }
    .mess__item__status{
        font-size: 12px;
    }
    .mess__left{
        background-color: #eaeaea;
    }
    
    .mess__wrap {
        .swiper-button{
            background-color: #eaeaea;
        }
    }
    .mess__right{
        margin-left: -20px;
        margin-right: -20px;
        border-radius: 0;
        border-top: 1px solid #f2f2f2;
    }
    .mess{
        margin-top: 0;
    }
    .mess__list__item{
        padding: 15px 20px;
    }
    .mess__list__item__top .data{
        display: none;
    }
    .mess__list__item__text{
        line-height: 1.3;
    }
    .dark-theme{
        .mess__left{
            background-color: #242123;
        }
        .mess__wrap {
            .swiper-button{
                background-color: #242123;
            }
        }
        .mess__right{
            border-top: 1px solid #292628;
        }
    }
    .mess__right__bot{
        justify-content: center;
    }
    .mess__right textarea{
        min-height: 102px;
        font-size: 12px;
        padding: 20px;
    }
    .mess__right{
        padding: 20px;
        padding-bottom: 40px;
    }
    .mess{
        margin-bottom: -20px;
    }
}